export default {
	data() {
		return {
			id:0,
			info:[],
		}
	},
	onLoad(opt) {
		this.id = opt.id
		getApp().user_api(true);
		this.noticeIist();
		
	},
	methods: {
		noticeIist(){
			let self = this;
			let path = '/gzh/article_api/visit'
			let data = {id:self.id};
			getApp().http(path, data, function(res) {
				self.info = res;
				console.log(res,'详情详情详情')
			})
		},
		indexUrl(icon){
			let http = icon.indexOf('http')
			if(http == -1){
				return this.webUrl +'/'+icon
			}else{
				return icon;
			}
		},
		
		
	}
}
